.display i{
    font-size: 16px;
    color: var(--desactiveView)
}
.display i:hover{
   cursor: pointer;
}
.display i:first-child{
    padding-right: 10px;
}
.gridView{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
    grid-gap: 1em;
    margin-top: 20px;
    padding-bottom: 100px;
}
.listView img{
    width: 100%;
}
.listView h3{
    font-size: 16px;
    margin-top: 20px;
    color: var(--darkGray);
    border-left: solid 2px var(--lightMainColor);
    padding-left: 5px;
}
.listView p {
    color: var(--lightGray);
    font-size: 14px;
}
.listView .movieTitle{
    padding-top: 20px;
}

