.wrapper{
    height: 100vh; 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
 .wrapper .overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #2f2f38;
    background: -webkit-gradient(linear, left top, left bottom, from(#2f2f38), color-stop(0%, rgba(24, 24, 38, 0)), to(black));
    background: linear-gradient(180deg, #2f2f38 0%, rgba(24, 24, 38, 0) 0%, black 100%);
}

.containerD{
    width: 75%;
    margin: 0 auto;
    z-index: 2;
    position: relative;
    padding-top: 100px;
} 
.containerD img {
    width: 260px;
    height: 400px;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}
.containerD .horizontal {
    display: flex;
}
.containerD .horizontalB {
    display: flex;
}
.containerD .horizontal .info{
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.containerD .horizontal .info h1{
    color: white;
    font-size: 30px;
    font-weight: bold;
}

.containerD .horizontal .info h6{
    color: #E0DDDD;
    font-size: 14px;
    font-weight: lighter;
    padding-bottom: 10px;
    margin-top: 30px;
}

.containerD .horizontal .info h5{
    color: #E0DDDD;
    font-size: 16px;
    font-weight: lighter;
    padding-top: 10px;
}

.containerD .horizontal .info i{
    color: white;
    font-size: 18px;
    font-weight: lighter;
    padding-top: 10px;
}
.containerD .horizontal .info .imdbIcon{
   font-size: 30px;
   margin: 0;
   padding: 0;
}
.containerD .horizontal .info .avarageVote{
    color: #E0DDDD;
    font-size: 18px;
    font-weight: lighter;
    background-color: none;
    margin-bottom: 20px;
}
.containerD .overview{
    color: #E0DDDD;
    font-size: 16px;
    border-left: solid 3px #F79822;
    padding-left: 5px;
    margin-top: 50px;
}
.containerD .description{
    color: #E0DDDD;
    font-size: 14px;
    font-weight: lighter;
}
.button{
    border-radius: 50px;
    border-style: none;
    padding: 0 15px;
    height: 40px;
    background: none;

}
.button:hover{
    box-shadow: 0 10px 15px -3px rgba(247, 152, 34, 0.1), 0 4px 6px -2px rgba(247, 152, 34, 0.5);
    transform: translateY(-10%);
    transition: transform .3s ease;
}
.containerD .horizontal .info .add {
    color: #F7AC18;
    border: solid 1px #FFB961;
    margin-right: 20px;
}
.containerD .horizontal .info .play {
    background-color: #F7AC18;
    color: black;
}
.wrapper .iframe{
    width: 900px;
}

@media only screen and (max-width: 768px) {
    .containerD{
        width: 90%;
    } 
    .containerD img {
        display: none;
    }
    .containerD .horizontal .info{
        margin-left: 0;
    }
}