:root{
  --mainColor: #F79822;
  --backgroundColor: #FDFCFA;
  --darkGray: #413E3E;
  --lightGray: #A2A2A2;
  --black: black;
  --lightMainColor: #FFB961;
  --desactiveView: #A2A2A2;
  --activeView: #FFB961;
}

.App {
  background: var(--backgroundColor); 
  min-height: 100vh;
}
.nav{
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.logo{
  color: var(--mainColor);
  font-size: 20px;
}
.fa-sun-o, .fa-moon-o{
  color: #A2A2A2;
}
.fa-sun-o{
 padding-right: 10px;
}
.fa-moon-o{
  padding-left: 10px;
}