.headerTitle{
    color: var(--black);
    font-size: 20px;
    border-left: solid var(--mainColor) 4px;
    padding-left: 8px;
}
.header:first-child{
    margin-top: 50px;
}
.container .header{
    display: flex;
    justify-content: space-between; 
}
.container .header a{
    color: var(--lightGray);
    text-decoration: none;
    font-size: 14px;
}
.container .header a:hover{
    text-decoration: underline;
}
.upcoming, .popular{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
    grid-gap: 1em;
    margin-top: 20px;
}
.upcoming{
    padding-bottom: 100px;
}
.popular{
    padding-bottom: 50px;
}
.cardM img{
    width: 100%;
}
.cardM:hover{
   transform: scale(1.05);
   transition: transform .3s ease;
}
.movieTitle {
    font-size: 16px;
    margin: 0;
    color: var(--darkGray);
}
.releaseDate{
    font-size: 12px;
    margin: 0;
    color: var(--lightGray);
    padding-top: 5px;
}
