.deleteDiv{
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 210px;
}
.fa-trash{
   font-size: 20px; 
   color: #F93943;
}
.fa-trash:hover{
   cursor: pointer;
}
.cardW img{
    width: 210px;
}
@media only screen and (max-width: 768px) {
    .deleteDiv{
        width: 100%;
    }
    .cardW img{
        width: 100%;
    }
}
