.wrapperH {
    display: flex;
    height: 100vh;
}
.wrapperH .left{
    flex: 1.5;
    padding-left: 200px;
}
.wrapperH .left .about{
   color: var(--lightGray);
   font-size: 14px;
   font-weight: lighter;
   padding-top: 130px;
}
.wrapperH .left .tmt{
   font-weight: bold;
   color: var(--mainColor);
   padding-bottom: 10px;
}
.wrapperH .left .description{
   font-weight: normal;
   color: var(--darkGray);
   font-size: 13px;
}
.wrapperH .left .author{
    color: var(--darkGray);
    font-size: 12px;
}
.wrapperH .right {
    flex: 2;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media only screen and (max-width: 768px) {
    .wrapperH .left{
        padding: 0 20px;
        flex: 1;
    }
    .wrapperH .right {
        display: none;
    }
}
